import { Component, OnInit, ViewChild, ChangeDetectorRef } from '@angular/core';
import { MatPaginator, MatSort, MatTableDataSource } from '@angular/material';
import { TransactionGroupMaintenanceServiceService } from '../transaction-group-maintenance-service.service';
import Swal from 'sweetalert2';
import { RoleService } from '../../roles1/roles.service';
import { permissionsLabels } from '../../roles1/models/fmosNewRolePermissions';

@Component({
  selector: 'app-global-gl-ac-code-maint-summary',
  templateUrl: './global-gl-ac-code-maint-summary.component.html',
  styleUrls: ['./global-gl-ac-code-maint-summary.component.css']
})
export class GlobalGlAcCodeMaintSummaryComponent implements OnInit {
  UserId: any;
  userData: any;
  trndata: any;
  trndata1: any;
  dataSource: any;
  dataSource1: any;
  res: boolean;
  roleCodes = new permissionsLabels();
 //added by gayathri issueid=0000571 on 21-08-2020
 @ViewChild('paginator1', {static: true}) paginator1: MatPaginator;
 @ViewChild('paginator2', {static: true}) paginator2: MatPaginator;
  @ViewChild(MatSort, { static: true }) sort: MatSort;

  displayedColumns: string[] = ['permission', 'trnCode', 'trnDesc', 'action'];
  displayedColumns1: string[] = ['permission', 'trnCode', 'trnDesc', 'action'];
  constructor(private transactionGroupService: TransactionGroupMaintenanceServiceService,
    private roleService: RoleService,
    private cdr: ChangeDetectorRef) { }

  ngOnInit() {
    this.getTrndata();
    this.getAccdata();
    this.getNewPermissions();
    this.roleService.screenLabelList.subscribe(message => this.roleCodes = message);

  }
  getNewPermissions() {
    this.roleService.fetchScreenPermissions('GlobalCodeMaintenance');
    this.cdr.markForCheck();
  }
  getTrndata() {
    this.transactionGroupService.getGlData().subscribe(trndata => {
      this.trndata = trndata;
      console.log('dat gl', this.trndata);
      if(this.trndata=='' && this.trndata1=='' )
      {
         Swal.fire('No GL/Account class code mapped for this user.')
       }
  else{
      if (this.trndata.length !== 0) {
        this.dataSource = new MatTableDataSource(this.trndata);
        this.dataSource.paginator = this.paginator1;
        this.dataSource.sort = this.sort;
      } else {
        // Swal.fire('No transaction code mapped for this user.');
        Swal.fire('No GL code mapped for this user');
      }
    }
    });
  }
  getAccdata() {
    this.transactionGroupService.getAccData().subscribe(trndata => {
      this.trndata1 = trndata;
      console.log(this.trndata1);
      if(this.trndata=='' && this.trndata1=='' )
      {
         Swal.fire('No GL/Account class code mapped for this user.')
       }
  else{
      if (this.trndata1.length !== 0) {
        this.dataSource1 = new MatTableDataSource(this.trndata1);
        this.dataSource1.paginator = this.paginator2;
        this.dataSource1.sort = this.sort;
      } else {
        // Swal.fire('No transaction code mapped for this user.');
        Swal.fire('No Account class code mapped for this user.');
      }
    }
    });
  }
  // getTransactionData()
  // {
  //   console.log("this is data from summary")
  //   this.transactionGroupService.gettingsummaryDataOfTransactionGroupMaintenance().subscribe(backendResp=>{
  //     console.log(backendResp);

  //   })
  deleteData(row) {
    console.log(row);
    // write a backend call.

    this.transactionGroupService.deleteGlData(row, this.UserId).subscribe(res => {
      this.res = res;
      if (this.res) {
        Swal.fire('Gl code deleted successfully.');
        // this.getTrndata();
        //added by vidya for issue id 705
        this.transactionGroupService.getGlData().subscribe(trndata => {
          this.trndata = trndata;
          this.dataSource = new MatTableDataSource(this.trndata);
        this.dataSource.paginator = this.paginator1;
        this.dataSource.sort = this.sort;
        });
      } else {
        Swal.fire('Unable to delete');
      }
    });
  }
  deleteDataAcc(row) {
    console.log(row);
    // write a backend call.

    this.transactionGroupService.deleteAcData(row, this.UserId).subscribe(res => {
      this.res = res;
      if (this.res) {
        Swal.fire('Account Class deleted successfully');
        // this.getAccdata();
        // added by vidya for issue id-705
        this.transactionGroupService.getAccData().subscribe(trndata => {
          this.trndata1 = trndata;
          this.cdr.markForCheck();
          this.dataSource1 = new MatTableDataSource(this.trndata1);
          this.dataSource1.paginator = this.paginator2;
          this.dataSource1.sort = this.sort;
        }); 
      } else {
        Swal.fire('Unable to delete');
      }
    });
  }
}
